
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Flexbox, FlexboxItem
},
data: () => ({
item: {},
itemList: ['推荐', '婚姻家庭', '交通事故', '劳动用工', '治安刑事', '医疗事故', '房产土地', '责权责务', '合同纠纷', '征地拆迁']
}),
mounted () {
const id = this.$route.query.id
if (this.$isEmptyParam(id)) {
this.$vux.toast.text('非法操作')
this.$router.go(-1)
return
}
this.$nextTick(() => {
this.getDetail(id)
})
},
methods: {
getDetail: function (id) {
this.$getData(this.$configs.api.issue, `/${id}`, response => {
this.item = response
})
}
}
}



